ader------------------------------ */\r\n\r\nheader {\r\n\tbackground-color: var(--grey);\r\n}\r\n\r\n#header-flex {\r\n\theight: 100%;\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tjustify-content: space-between;\r\n\tpadding: 10px;\r\n\tpadding-top: 20px;\r\n\tborder: 1px solid black;\r\n\tborder-top: none;\r\n}\r\n.joined,\r\n.flex-left,\r\n.buttonsLeft {\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tgap: 5px;\r\n}\r\n\r\n.joined {\r\n\tflex-wrap: wrap;\r\n\tgap: 30px;\r\n}\r\n\r\n.buttonsLeft {\r\n\tgap: 15px;\r\n}\r\n\r\n#logo {\r\n\twidth: 35px;\r\n\theight: 35px;\r\n\tpadding: 5px;\r\n}\r\n\r\nh4,\r\nh3 {\r\n\tcolor: rgb(255, 255, 255);\r\n\tfont-family: \"Schibsted Grotesk\", sans-serif;\r\n\tletter-spacing: 1.2px;\r\n}\r\n\r\nh4 {\r\n\tfont-size: smaller;\r\n}\r\n\r\na {\r\n\ttext-decoration: none;\r\n\tcolor: rgba(255, 255, 255, 0.799);\r\n}\r\n\r\na:hover {\r\n\ttext-decoration: underline;\r\n\tcolor: orange;\r\n}\r\n\r\n#navRight {\r\n\t/* padding-right: 30px; */\r\n}\r\n\r\n.navButton {\r\n\twidth: clamp(100px, 15vw, 150px);\r\n\theight: 30px;\r\n\tborder-radius: 15px;\r\n\tfont-size: 0.9rem;\r\n\tfont-weight: 600;\r\n\tcolor: rgb(0, 0, 0);\r\n\tborder: 1px solid rgba(0, 0, 0, 0.461);\r\n\tbackground-color: grey;\r\n}\r\n\r\n.navButton:hover,\r\n#addHoldingButton:hover,\r\n.modifyButton:hover,\r\n.editButtons:hover {\r\n\tborder: 1px solid orange;\r\n\tcursor: pointer;\r\n\tbackground-color: rgba(0, 0, 0, 0.495);\r\n\tcolor: rgba(255, 255, 255, 0.799);\r\n\tscale: 1.01;\r\n\ttransition: 0.5s;\r\n}\r\n\r\n/* -----------------------main------------------- */\r\n\r\nmain {\r\n\tdisplay: grid;\r\n\tgrid-template-rows: 1fr;\r\n\tgrid-template-columns: minmax(250px, 20vw) 1fr;\r\n}\r\n\r\n.portfolioSide {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\tpadding: 35px;\r\n\tborder-left: 1px solid orange;\r\n}\r\n.mainTop {\r\n\tdisplay: flex;\r\n\talign-items: center;\r\n\tjustify-content: space-between;\r\n\tpadding-bottom: 20px;\r\n}\r\n\r\n.t