: none;\r\n\tcursor: pointer;\r\n\tscale: 0.75;\r\n\tborder-radius: 5px;\r\n\twidth: 50px;\r\n\theight: 30px;\r\n}\r\n\r\n.dropdown-content {\r\n\tposition: absolute;\r\n\ttop: 100%;\r\n\tright: 0px;\r\n\tmin-width: 120px;\r\n\tdisplay: none;\r\n\tbox-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);\r\n\tz-index: 1;\r\n\tborder-bottom-left-radius: 10px;\r\n\tborder-bottom-right-radius: 10px;\r\n\tborder-top-left-radius: 10px;\r\n\tbackground-color: #373737;\r\n}\r\n\r\n.dropdown-content a {\r\n\tcolor: #333;\r\n\tpadding: 8px 16px;\r\n\tdisplay: block;\r\n\ttext-decoration: none;\r\n}\r\n\r\n.dropdown:hover .dropdown-content {\r\n\tdisplay: flex;\r\n\ttransition: 0.5s;\r\n\tflex-direction: column;\r\n\tborder: 1px solid orange;\r\n}\r\n\r\n.modifyButton {\r\n\tbackground-color: #373737;\r\n\tborder-radius: 10px;\r\n\tborder: 1px solid #ffffff21;\r\n\tcolor: #ffffff21;\r\n\twidth: 30px;\r\n\theight: 30px;\r\n}\r\n\r\n.modifyButton:hover {\r\n\tborder: 1px solid orange;\r\n\tborder-radius: 0px;\r\n}\r\n\r\n.editButtons {\r\n\tborder-bottom-left-radius: 10px;\r\n\tborder-bottom-right-radius: 10px;\r\n\tborder-top-left-radius: 10px;\r\n\tbackground-color: #373737;\r\n}\r\n\r\n.heading {\r\n\tdisplay: none;\r\n}\r\n\r\n.visible {\r\n\tdisplay: inline;\r\n\tfont-size: 0.85rem;\r\n\tmargin-bottom: 5px;\r\n}\r\n\r\n/* -------------------------radio------------ */\r\n\r\n.radio {\r\n\tbackground: #454857;\r\n\tpadding: 4px;\r\n\tborder-radius: 3px;\r\n\tbox-shadow: inset 0 0 0 3px rgba(35, 33, 45, 0.3),\r\n\t\t0 0 0 3px rgba(185, 185, 185, 0.3);\r\n\tposition: relative;\r\n}\r\n\r\n.radio input {\r\n\twidth: auto;\r\n\theight: 100%;\r\n\t-webkit-appearance: none;\r\n\t-moz-appearance: none;\r\n\tappearance: none;\r\n\toutline: none;\r\n\tcursor: pointer;\r\n\tborder-radius: 2px;\r\n\tpadding: 4px 8px;\r\n\tbackground: #454857;\r\n\tcolor: #bdbdbdbd;\r\n\tfont-size: 14px;\r\n\tfont-family: system-ui, -apple-system, BlinkMacSystemFont, \"Segoe UI\", Roboto,\r\n\t\t\"Helvetica Neue\", Arial, \"Noto Sans\", sans-serif, \"Apple Color Emoji\",\r\n\t\t\"Segoe UI Emoji\", \"Segoe UI Symbol\", \"Noto Color Emoji\";\r\n\ttransition: all 100ms linear;\r\n}\r\n\r\n.radio input:checked {\r\n\tbackground-image: linear-gradient(180deg, #95d891, #74bbad);\r\n\tcolor: #fff;\r\n\tbox-shadow: 0 1px 1px #0000002e;\r\n\ttext-shadow: 0 1px 0px #79485f7a;\r\n}\r\n\r\n.radio input:before {\r\n\tcontent: attr(label);\r\n\tdisplay: inline-block;\r\n\ttext-align: center;\r\n\twidth: 100%;\r\n}\r\n/* --------------------------------------------- */\r\n"],"sourceRoot":""}]);
